import React from 'react';
import { ButtonComponent } from './button.style';
import { ButtonProps } from './button.props';

export const Button = ( {
    buttonType="primary",
    href,
    isFullWidth = false,
    children,
    ...restProps
}: ButtonProps) => {
    return (
        <ButtonComponent as={href ? 'a' : 'button'} href={href} buttonType={buttonType} {...restProps}>{children}</ButtonComponent>
    );
};